:root{
    --whitee-color:rgb(255, 255, 255);
    --blue:rgb(255, 255, 255);
    --title:rgb(156, 156, 156);
    --background-color: black;
    
}

.light-theme-variables{
    --whity-color:rgb(255, 255, 255);
    --background-color:white;
    --blue:rgb(0, 0, 175);
    --title:rgb(126, 126, 126);
    
}

.light-theme-variables p{
color: rgb(32, 32, 32);
}

