.light-theme-variables #text-white{
    color:rgb(255, 255, 255);
}

.light-theme-variables #text-blue{
 color:blue;
}

.light-theme-variables #text-black{
 color:rgb(37, 37, 37);
}
.light-theme-variables #text-title{
 color:rgb(117, 117, 117);
}


body{
    background-color: var(--background-color);
}








.active-text{
    color: #ffffff;
    font-weight: bold;
    border-top: white 3px;
}

.inactive-text{
    color: #f0f0f0ee;
}
.active{
    color: #ffffff;
   
}

.active:active{
    border-top: 3px white;
}

.inactive{
    color: #f0f0f0ee;
}


/* theme-Toggle component */
.switch-mode{
    height: 2rem;
    width: 3.5rem;
    background: rgb(78, 78, 78);
    position: relative;
    display: flex;
    justify-content: flex-start;
    border-radius: 1rem;
    cursor: pointer;
    padding: 0.25rem;

}

.switch-mode >div{
    background: white;
    height: 1.4rem;
    width: 1.4rem;
    border-radius: 50%;
}

.light{
    justify-content: flex-end;
}

